    bankData.value = response.data;
  });
}

function makeMap(update = false) {
  if (!mapData.value) {
    mapData.value = L.map("leafletmap").setView([41.99646, 21.43141], 13);

    L.tileLayer("https://tile.openstreetmap.org/{z}/{x}/{y}.png", {
    maxZoom: 19,
    attribution:
